body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F7FFF7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tile-title {
    color: white;
    opacity: .7;
    font-weight: 700;
    position: absolute;
    bottom: 30px;
    left: 40px;
    -webkit-transition: bottom 0.2s, left 0.2s, opacity 0.2s;
    transition: bottom 0.2s, left 0.2s, opacity 0.2s;
    font-size: 1em;
    line-height: 1em;
}

.is-hovered .tile-title {
    bottom: 34px;
    left: 44px;
    opacity: .8;
}

.name {
    grid-area: name;
    background-color: #292F36;
}

.name .tile-title {
    bottom: 36px;
    left: 40px;
    max-width: 260px;
    opacity: .9;
    font-size: 1.2em;
    line-height: .9em;
}

.name .tile-title div:nth-child(1) {
    margin-bottom: 5px;
}

.name .tile-title div:nth-child(2) {
    font-size: .5em;
    line-height: 1.2em;
    margin-top: 5px;
}

.about {
    grid-area: about;
    background-color: #058ED9;
}

.work {
    grid-area: work;
    background-color: #FF6B6B;
}

.tech {
    grid-area: tech;
    background-color: #99a6b2;
}

.contact {
    grid-area: contact;
    background-color: #FFE66D;
}

.resume {
    grid-area: resume;
    opacity: .9;
}

.resume .tile-title {
    opacity: .9;
    color: #292F36;
    font-weight: 900;
    font-size: 2.1em;
    -webkit-transform: translatex(-50%) translateY(-50%);
    transform: translatex(-50%) translateY(-50%);
    top: 50%;
    left: 50%;
    bottom: 0;
}

@media (max-width: 1200px) {

}

@media (max-width: 1000px) {
    .tile-title {
        font-size: .8em;
        bottom: 25px;
        left: 30px;
    }

    .is-hovered .tile-title {
        bottom: 29px;
        left: 34px;
    }

    .name .tile-title div:nth-child(1) {
        margin-bottom: 5px;
    }

    .name .tile-title div:nth-child(2) {
        margin-top: 5px;
    }

    .resume .tile-title {
        -webkit-transform: translatex(-50%) translateY(-50%) rotate(270deg);
        transform: translatex(-50%) translateY(-50%) rotate(270deg);
        font-size: 1.5em;
        width: 165px;
        height: 37px;
    }

    .resume .tile-title > div {
        display: inline-block;
    }
}

@media (max-width: 650px) {
    .tile-title {
        position: absolute;
        bottom: 28px;
        left: 28px;
    }

    .name .tile-title {
        max-width: 210px;
        margin: 0 auto;
        top: 50%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
        left: 0;
        right: 0;
    }

    .name .tile-title div:nth-child(1) {
        margin-bottom: 5px;
        font-size: .8em;
    }
    .name .tile-title div:nth-child(2) {
        font-size: .4em;
    }

    .resume .tile-title {
        -webkit-transform: translatex(-50%) translateY(-50%) rotate(360deg);
        transform: translatex(-50%) translateY(-50%) rotate(360deg);
        font-size: 1.5em;
        height: auto;
    }

    .resume .tile-title > div {
        display: block;
    }
}

@media (max-width: 460px) {
    .about .tile-title {
        -webkit-transform: rotate(270deg) translateY(-50%);
        transform: rotate(270deg) translateY(-50%);
        bottom: 50px;
        left: 0px;
    }

    .about .tile-title {
        left: 15px;
        bottom: 52px;
    }

    .is-hovered.about .tile-title {
        left: 19px;
        bottom: 53px;
    }

    .resume .tile-title {
        -webkit-transform: translatex(-55%) translateY(-50%) rotate(270deg);
        transform: translatex(-55%) translateY(-50%) rotate(270deg);
        height: 70px;
        width: 85px;
        font-size: 1.4em;
    }
}

.page-wrapper {
    height: 100vh;
    min-height: 620px;
    max-height: 100%;
    width: 100vw;
    min-width: 316px;
    max-width: 100%;
    justify-content: center;
    text-align: center;
}

.grid-container {
    display: grid;
    grid-template-areas:
            'name name name about about about'
            'name name name work work work'
            'tech tech contact contact contact resume';
    grid-gap: 40px;
    height: 91%;
    width: 93%;
    margin: 0 auto;
    padding: 3% 0;
    font-size: 30px;
}
.grid-container > div {
    font-size: .8em;
    text-align: center;
    position: relative;
    box-shadow: 2px 1px 1px lightgrey;
    cursor: pointer;
}

.grid-container > div:before {
     content: '';
     position: absolute;
     border: white solid 12px;
     top: 5px;
     left: 5px;
     right: 5px;
     bottom: 5px;
     opacity: .4;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-property: border;
    transition-property: border;
 }

.grid-container > div:hover:before {
    border: white solid 16px;
    opacity: .9;
}

.grid-container > div:first-child {
    font-size: 1em;
    text-align: left;
    cursor: default;
    opacity: .9;
}

.grid-container > div:first-child:before  {
    border: white solid 16px;
    opacity: .9;
    -webkit-transition-property: none;
    transition-property: none;
    top: 7px;
    left: 7px;
    right: 7px;
    bottom: 7px;
}

.grid-container > div:first-child:hover:before  {
    border: white solid 16px;
}

.grid-container > div:last-child {
    box-shadow: none;
}

.grid-container > div:last-child:before {
    opacity: .9;
    border: none !important;
}

@media (max-width: 1000px) {
    .grid-container {
        grid-template-areas:
                'name name name name name name'
                'work work about about about about'
                'tech tech tech contact contact resume';
        grid-gap: 30px;
        height: 93%;
        width: 95%;
        margin: 0 auto;
        padding: 3% 0;
    }
}

@media (max-width: 650px) {
    .grid-container {
        grid-template-areas:
                'name name name'
                'name name name'
                'about work work'
                'about tech tech'
                'contact contact resume';
        grid-gap: 10px;
        height: 96%;
        width: 96%;
        margin: 0 auto;
        padding: 3% 0;
    }

    .grid-container > div:before {
        border: white solid 10px;
    }

    .grid-container > div:hover:before {
        border: white solid 12px;
    }
}

@media (max-width: 460px) {
    .page-wrapper {
        min-height: auto;
    }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

